<template>
	<div class="index">
		<transition name="bounce">
			<x-login v-if="showLogin" @closeLogin="closeLogin"></x-login>
		</transition>
		<template v-if="!showLogin">
			<x-header  @openLogin="closeLogin"></x-header>
			<router-view></router-view>
		</template>
	</div>
</template>

<script>
	/* import io from 'socket.io-client'; */
	import XLogin from '../components/x-login/XLogin.vue'
	import XHeader from '../components/x-header/XHeader.vue'
	import XContent from '../components/x-content/XContent.vue'
	import XEditor from '../components/x-editor/XEditor.vue'
	
	export default {
		name: 'app',
		data() {
			return {
				showLogin: true,
				
			}
		},
		components: {
			XHeader,
			XContent,
			XLogin,
			XEditor
		},
		methods:{
			closeLogin(val) {
				this.showLogin = val
			}
		}
	}
	
</script>

<style scoped>
 .index{
	 width: 100%;
	 height: 100%;
 }
</style>
