import Vue from 'vue'
import Router from 'vue-router'
import IndexPage from './views/IndexPage.vue'
import XContent from './components/x-content/XContent.vue'
import XEditor from './components/x-editor/XEditor.vue'
Vue.use(Router)

export default new Router({
	routes: [{
		path: '/',
		component: IndexPage,
		children: [{
				path: '/',
				component: XContent
			},
			{
				path: 'editor',
				component: XEditor
			}
		]
	}]
})

