import Vue from 'vue'
import Vuex from 'vuex'

const XZ_KEY =	'xz-name'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    uid: ''
  },
  mutations: {
    getName(state) {
		if(!localStorage.getItem(XZ_KEY)) {
			const val = 'It_' + Math.random().toString().substr(2,8)
			localStorage.setItem(XZ_KEY, val)
			state.uid = val
		} else {
			state.uid = localStorage.getItem(XZ_KEY);
		}
	}
  }
})

export default store