<template>
	<div class="read-main-bac" @click="close">
	<div class="read-main" ref="note" v-html="markContent">
		
	</div>
	
</div>

</template>

<script>
	import marked from 'marked'
	import hljs from 'highlight.js/lib/highlight';
	import javascript from 'highlight.js/lib/languages/javascript';
	import 'highlight.js/styles/atom-one-dark.css'
	hljs.registerLanguage('javascript', javascript);

	export default {
		props:['content'],
		data() {
			return {
				control:true
			}
		},
		
		name: 'app',
		mounted() {
		
		},
		computed: {
			markContent() {
				console.log(marked(this.content))
				return marked(this.content);
			}
		},
		created() {
			hljs.initHighlightingOnLoad()
			var rendererMD = new marked.Renderer();
			marked.setOptions({
				renderer: rendererMD,
				gfm: true,
				tables: true,
				breaks: false,
				pedantic: false,
				sanitize: false,
				smartLists: true,
				smartypants: false,
				highlight: function(code) {
					return hljs.highlightAuto(code).value
				}
			})
		},
		methods: {
			close() {
				if(!this.control) return
				this.control = false
				this.$bac.remove()
				this.$emit('closeRead')
			}
		}

	}
</script>

<style >
	.read-main-bac{
		left: 0px;
		top: 0px;
		position: absolute;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		box-sizing: border-box;
		padding-top: 10px;
		z-index: 10;
	}
	.read-main {
		
		background: white;
		width: 800px;
		height: 700px;
		z-index: 2;
		padding: 20px;
		overflow-y: scroll;
		border-radius:10px;
	}
	
	.read-main::-webkit-scrollbar{
		display: none;
	}
	
	ol,ul{
		box-sizing: border-box;
		padding-left: 20px;
	}

	
	

	pre {
		background: #282c34;
		display: block;
		overflow-x: auto;
		padding: 0.5em;
		color: #abb2bf;
		font-family: monospace;
		white-space: pre;
	}
	
	
</style>


