import {baseSrc} from './baseSrc.js'
import request from './http.js'
import axios from 'axios'

export const getToken = function() {
	let url = baseSrc
	let methods = 'get'
	return request({}, url, methods)
}


export  const sumbitEssay = function(params) {
	let url = baseSrc + 'essay'
	let methods = 'post'
	return request(params, url, methods)
}