<template>
	<div class="login-main" @mousemove="moveImg($event)">
		<div class="title">Welcome to Xz-Blog</div>
		<div class="log-box">
			<div class="log-item"><label class="tip" for="name">账 号 :</label>
			<input type="text" id="name"/></div>
			<div class="log-item"><label class="tip"  for="password">密 码 :</label>
			<input type="password" value="" id="password"/></div>
		</div>
		
		<div class="button-box">
			<div class="left">注册</div>
			<div class="rigth" >登陆</div>
		</div>
		
		<div class="o-login" @click="close">游客登陆</div>
		
		<div class="three-box">
			<div class="moon" ref="moon"><img src="../../assets/green.png" alt=""></div>
			<div class="lake" ref="lake"><img src="../../assets/lake.png" alt=""></div>
			<div class="tow1" ref="tow1"><img src="../../assets/tower.png" alt=""></div>
			<div class="tow2" ref="tow2"><img src="../../assets/tower.png" alt=""></div>
			<div class="tow3" ref="tow3"><img src="../../assets/tow3.png" alt=""></div>
			<div class="point1" ref="point1"><img src="../../assets/point3.png" alt=""></div>
			<div class="point2" ref="point2"><img src="../../assets/point2.png" alt=""></div>
		</div>
	</div>
</template>

<script>
	import { mapMutations } from 'vuex'
	export default {
		data() {
			return {
				x: 0,
				y: 0
			};
		},
		created() {
			
		},
		methods:{
			moveImg(e) {
				let x_move = e.movementX < 0 ? -1 : 1;
				let y_move = e.movementY < 0 ? -1 : 1;
				for(let i = 0;i < Object.keys(this.$refs).length; i++){
					this.x = this.x+0.02*x_move;
					this.y = this.y+0.02*y_move;
					let val_x = this.x + 'px'
					let val_y = this.y + 'px'
					this.$refs[Object.keys(this.$refs)[i]].style.transform = `translate(${val_x},${val_y})`
				}
			},
			
			close() {
				this.getName();
				this.$emit('closeLogin', false)
			},
			...mapMutations(['getName'])
		}
	}
</script>

<style scoped>
	.login-main {
		background-color: rgb(32, 25, 50);
		background-image: url(../../assets/login-bac.png);
		height: 100%;
		background-repeat: no-repeat;
		color: white;
		position: absolute;
		width: 100%;
	}

	.title {
		color: white;
		box-sizing: border-box;
		padding-left: 300px;
		padding-top: 40px;
		background-size: contain;
		background-repeat: no-repeat;
		font-size: 30px;
		font-weight: bold;
		margin-bottom: 200px;
	}
	
	.log-box{
		padding-left: 280px;
		font-size: 22px;
		position: relative;
	}
	
	.log-box:before{
		content: "";
		background: url(../../assets/left.png);
		left: 150px;
		position: absolute;
		top: -30px;
		width: 92px;
		height: 70px;
		background-repeat: no-repeat;
		background-size: contain;
	}
	
	.log-item{
		margin-bottom: 50px;
		
	}
	
	.log-box input:focus{
		color:rgb(7,206,140) ;
	}
	.log-box input{
		font-size: 18px;
		background: none;
		border: none;
		border-bottom: 1px solid white;
		box-sizing: border-box;
		margin-left: 10px;
		color: white;
		width:260px;
		text-indent: 5px;
	}
	
	.log-box input:focus {
		outline: none;
		color: darkorange;
		border-bottom: 1px solid rgb(7,206,140);
	}
	
	.button-box{
		padding-left: 320px;
		display: flex;
	}
	
	.button-box div{
		width: 120px;
		height: 50px;
		font-size: 22px;
		background-image: linear-gradient(to bottom, #00D58E, rgb(0,179,94));
		border-radius:8px;
		text-align: center;
		line-height: 50px;
		cursor: pointer;
		margin-right: 50px;
		user-select: none;
	}
	
	.o-login{
		margin-left: 200px;
		margin-top: 170px;
		width: 240px;
		height: 100px;
		font-size: 26px;
		background-image: linear-gradient(to bottom, orange, darkorange);
		border-radius:8px;
