		cursor: pointer;
		margin-right: 50px;
		user-select: none;
		letter-spacing: 3px;
	}
	
	
	.three-box{
		position: absolute;
		width: 600px;
		height: 500px;
		
		left:60%;
		top: 170px;
		transform-style: preserve-3d;
	}
	
	.three-box div{
		position: absolute;
		left: 0px;
		top: 0px;
	}
	.three-box .moon{
		width: 400px;
